export default {
  name: "unofficialCourses",
  data() {
    return {
      swiperOption: {
        autoplay: {
          delay: 5000 //1秒切换一次
        },
        speed: 1000, // 轮播速度
        loop: true, //开启轮播图前后循环模式
        // 设置轮播点,并且允许点击切换
        direction: "vertical",
        height: 100
      },
      option: {
        lastList: [
          { name: "课程章节", class: "", type: true },
          { name: "课程详情", class: "", type: false }
        ]
      }
    };
  },
  //   直接执行
  mounted() {},
  //   方法
  methods: {
    // 公用的选择方法
    selectRankingAll: function(item, index, ListName) {
      const _this = this;
      _this.option[ListName].map(function(event, idx) {
        if (event.name == item.name) {
          event.type = true;
          _this.$set(_this.option[ListName], idx, event);
        } else {
          event.type = false;
          _this.$set(_this.option[ListName], idx, event);
        }
      });
    },
    openCustomHtml: function(name) {
      if (name) {
        if (name == "直播") {
          this.$router.push({ path: "/curriculumLiveBroadcast" });
        }
         if (name == "购买") {
          this.$router.push({ path: "/buyCurriculum" });
        }
      }
    }
  }
};



